/*
 * Generic grid styles
 */
.grid {
  margin: 0 auto;
  max-width: 960px;
  overflow: hidden;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -0.5em 0 -0.5em;
}
.row > * {
  box-sizing: border-box;
  padding: 0 0.5em 1em;
  flex-basis: 100%;
}
.row:last-child {
  margin-bottom: -1em;
}

.gutterless > * {
  margin: 0;
}
.gutterless > * > * {
  padding: 0;
}
.gutterless > *:last-child {
  margin-bottom: 0;
}

.col {
  flex: 1 0 auto;
}

/*
 * Default view
 */
div.col-1 {
  flex-basis: 8.3333333333%;
}

div.col-2 {
  flex-basis: 16.666666666%;
}

div.col-3 {
  flex-basis: 25%;
}

div.col-4 {
  flex-basis: 33.333333333%;
}

div.col-5 {
  flex-basis: 41.666666666%;
}

div.col-6 {
  flex-basis: 50%;
}

div.col-7 {
  flex-basis: 58.333333333%;
}

div.col-8 {
  flex-basis: 66.666666666%;
}

div.col-9 {
  flex-basis: 75%;
}

div.col-10 {
  flex-basis: 83.333333333%;
}

div.col-11 {
  flex-basis: 91.666666666%;
}

div.col-12 {
  flex-basis: 100%;
}

div.col-top {
  align-self: flex-start;
}

div.col-bottom {
  align-self: flex-end;
}

div.col-middle {
  align-self: center;
}

.row-left {
  justify-content: flex-start;
}

.row-right {
  justify-content: flex-end;
}

.row-center {
  justify-content: center;
}

.row-space-between {
  justify-content: space-between;
}

.row-space-around {
  justify-content: space-around;
}

.row-reverse {
  flex-direction: row-reverse;
}

.row-equal > * {
  display: flex;
}

.row-top {
  align-items: flex-start;
}

.row-middle {
  align-items: center;
}

.row-bottom {
  align-items: flex-end;
}

.hide {
  display: none;
}

